import React, { useEffect, useState } from "react";
import { withRouter } from "react-router-dom";
import "./404page.css";
import Axios from "axios";

function Notfound(props) {
  const [views, setviews] = useState("");
  useEffect(() => {
    Axios.get(" https://blogrizky.herokuapp.com/api/notfound")
      .then(result => {
        setviews(result.data.views);
      })
      .catch(err => {
        console.log(err);
      });
  }, []);

  return (
    <div className="notfoundcontainer">
      <div className=" notfound">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <span className="notfoundtext">
                Whoops . it seems that you are Lost
              </span>
              <br />
              <span className="notfoundtext">Don't worry i am here</span>
              <br />
              <button
                onClick={() => props.history.push("/")}
                className="btn notfounditems large btn-primary"
              >
                {" "}
                let's try from the begining{" "}
              </button>
              <br />
              <span className="notfoundtextbottom">
                {views} people have lost so far
              </span>
            </div>
          </div>
          <br />
        </div>
      </div>
    </div>
  );
}
export default withRouter(Notfound);
