import React, { useState, useEffect } from "react";
import Markdown from "markdown-to-jsx";
import "./blogpost.css";
import Gist from "./gist";
import { Offline, Online, Detector } from "react-detect-offline";
import Footerblogpost from "../../components/footer/footerblogpost";
import Axios from "axios";
import Navbar from "../../components/Navbar/navbar";
import Modal from "react-responsive-modal";
const moments = require("moment");
const API = "https://blogrizky.herokuapp.com";

export default function Blogpost(props) {
  // const [Data, setData] = useState([]);
  const [markdown, setmarkdown] = useState("");
  const [postdata, setpostdata] = useState({});
  const [open, setopen] = useState(false);
  const [comment, setcomment] = useState("");
  const [displayname, setdisplayname] = useState("");
  const [email, setemail] = useState("");
  const [postid, setpostid] = useState("");
  const [comments, setcomments] = useState([]);

  const [onceoffline, setonceoffline] = useState(false);
  useEffect(() => {
    // console.log("id- ");
    // console.log(props.match.params.id);
    const id = props.match.params.id;
    setpostid(id);

    Axios.get(`${API}/static/${id}.md`)
      .then(result => {
        // console.log(result);
        setmarkdown(result.data);
      })
      .catch(err => {
        console.log(err);
        props.history.push("/404");
      });

    Axios.get(`${API}/api/getcomments/${id}`)
      .then(result => {
        // console.log("comment data");
        // console.log(result.data);
        setcomments(result.data);
      })
      .catch(err => {
        console.log(err);
      });

    Axios.get(`${API}/api/getpost/${id}`)
      .then(result => {
        // console.log("post data");
        // console.log(result.data);
        setpostdata(result.data);
      })
      .catch(err => {
        console.log(err);
      });

    Axios.post(`${API}/api/blog`, {
      location: window.location.href
    })
      .then(result => {
        // console.log(result.data);
      })
      .catch(err => {
        console.log(err);
      });

    // console.log(typeof id);

    //

    // console.log(iidd);
    // if (isValid(id)) {
    //   var iidd = parseInt(id);

    //   if (iidd > entries.length || iidd < 0) {
    //     console.log("limites off");
    //     console.log("404");
    //     props.history.push("/404");
    //   } else {
    //     var idd = parseInt(id);

    //     setentry(entries[idd]);

    //     const mdfile = require(`../../entries/${entries[idd].path}.md`);

    //     fetch(mdfile)
    //       .then(res => res.text())
    //       .then(result => {
    //         setmarkdown(result);
    //       })
    //       .catch(err => {
    //         console.log(err);
    //       });
    //   }
    // } else {
    //   console.log("404");
    //   props.history.push("/404");
    // }
  }, []);

  // function isValid(n) {
  //   return /^-?[\d.]+(?:e-?\d+)?$/.test(n);
  // }

  const commentHandle = e => {
    e.preventDefault();

    var payload = {
      displayName: displayname,
      email: email,
      content: comment,
      postid: postid
    };
    Axios.post(`${API}/api/newcomment`, payload)
      .then(result => {
        // console.log(result.data);
        setcomment("");
        if (result.data.err === false) {
          onCloseModal();
          Axios.get(`${API}/api/getcomments/${postid}`)
            .then(result => {
              // console.log("comment data");
              // console.log(result.data);
              setcomments(result.data);
            })
            .catch(err => {
              console.log(err);
            });
        }
      })
      .catch(err => {
        console.log(err);
      });
  };

  const onOpenModal = () => {
    setopen(true);
  };

  const onCloseModal = () => {
    setopen(false);
  };

  return (
    <div className="blogpostcontainer">
      <Navbar />
      <Detector
        onChange={con => {
          // console.log("con connect;assdkak");
          // console.log(con);
          setonceoffline(true);
        }}
        render={({ online }) => <div></div>}
      />
      <Offline>
        <div className="offlinediv">You appears to be offline</div>
      </Offline>
      <Online>
        <div hidden={!onceoffline} className="onlinediv">
          Welcome Back
        </div>
      </Online>

      <Modal
        // className="commentmodal"
        open={open}
        onClose={onCloseModal}
        closeIconSize={20}
        styles={{ modal: { color: "black", width: "50%" } }}
        center
      >
        <h4>i'd love to here your comments</h4>
        <form onSubmit={commentHandle}>
          <div class="form-group">
            <input
              type="text"
              required
              placeholder="your name"
              class="form-control"
              onChange={e => setdisplayname(e.target.value)}
            ></input>
          </div>
          <div class="form-group">
            <input
              class="form-control"
              type="email"
              required
              placeholder="your email "
              onChange={e => setemail(e.target.value)}
            ></input>
          </div>

          <button className="btn btn-primary" type="submit">
            Comment
          </button>
        </form>
      </Modal>
      <div class="container ">
        <div class="row">
          <div class="col-lg-9">
            <h1 class="mt-4">{postdata.title}</h1>

            <hr />
            {/* {console.log(new Date().toISOString())} */}
            <p>Posted {moments(postdata.date).fromNow()} </p>

            <hr />

            <Markdown
              children={markdown}
              options={{
                overrides: {
                  GithubGist: {
                    component: Gist
                  }
                }
              }}
            />

            <div class="card my-4 commentsection text-white ">
              <h5 class="card-header  ">Leave a Comment:</h5>
              <div class="card-body ">
                <form>
                  <div class="form-group">
                    <textarea
                      class="form-control"
                      rows="3"
                      value={comment}
                      onChange={e => setcomment(e.target.value)}
                    ></textarea>
                  </div>
                  <button
                    onClick={onOpenModal}
                    type="submit"
                    class="btn btn-primary"
                  >
                    Comment
                  </button>
                </form>
              </div>
            </div>

            {comments.map((ele, index) => {
              // console.log("brrrrrrrrrrrr");
              return (
                <div class="media mb-4">
                  <img
                    class="d-flex mr-3 rounded-circle"
                    src="https://img.icons8.com/pastel-glyph/64/000000/person-male.png"
                    alt=""
                  />
                  <div class="media-body">
                    <div className="commentheaderdiv">
                      <h3 class="mt-0">{ele.displayName}</h3>{" "}
                      <span className="commentfromnow">
                        {" "}
                        {moments(ele.date).fromNow()}{" "}
                      </span>
                    </div>

                    {ele.content}
                  </div>
                </div>
              );
            })}
          </div>

          <div class="col-lg-3">
            <div class="card my-4 commentsection">
              <h5 class="card-header">Views</h5>
              <div class="card-body">
                <div class="input-group">
                  <h5>{postdata.count}</h5>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footerblogpost />
    </div>
  );
}
